<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>
            <span>REPORT ISSUE</span>
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields
                    marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Title</label>
                        <input
                          type="text"
                          v-model="title"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[1]"
                        >{{ errors[1] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Issue</label>
                        <input
                          type="text"
                          v-model="issue"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[1]"
                        >{{ errors[1] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Priority</label>
                        <select
                          v-model="priorite"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        >
                          <option
                            :value="issue.id"
                            class="font-weight-bold"
                            v-for="issue in priorities"
                            :key="issue.id"
                          >{{ issue.name }}</option>
                        </select>
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{ errors[2] }}</span>
                      </div>
                    </div>

                    <!--acttive -->
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Issue In</label>
                        <select
                          v-model="issuein"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        >
                          <option
                            :value="issue.id"
                            class="font-weight-bold"
                            v-for="issue in issuesin"
                            :key="issue.id"
                          >{{ issue.name }}</option>
                        </select>
                      </div>
                    </div>

                    <div class="form-row">
                      <!-- image -->
                      <div class="col-12 text-center">
                        <label class="btn btn-sm btn-info text-center" style="color:#fff">
                          <i class="fas fa-image pr-2"></i>
                          <div class="d-inline font-weight-bold">ADD IMAGE</div>
                          <input type="file" class="file" ref="image" />
                        </label>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <button
                          type="reset"
                          class="btn btn-sm btn-secondary"
                          @click="disableId = false"
                        >
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </button>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{ errors[1] }}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5">
          <div class="card shadow p-0 ml-2">
            <h5 class="card-header font-weight-bold">
              <i class="fas fa-list pl-1 pr-3"></i>Issues
            </h5>
            <div class="row">
              <div class="col-sm-12">
                <div class="row p-3">
                  <div class="col-sm-12">
                    <table>
                      <tr>
                        <th>S.No.</th>
                        <th class="pl-2">Title</th>
                        <th class="pl-2">Priority</th>
                        <th class="pl-2">Resolved</th>
                      </tr>
                      <tr v-for="(issue, index) in issues" :key="issue.id">
                        <td>{{index+1}}</td>
                        <td>{{ issue.title }}</td>
                        <td class="pl-2">{{ issue.priority }}</td>
                        <td class="pl-2">
                          <span v-if="issue.resolved">{{ issue.resolved }}</span>
                          <span v-else>Pending</span>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import { firestorage } from "@/firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import axios from "axios";

export default {
  name: "Issues",
  data() {
    return {
      title: null,
      issue: null,
      image: null,
      priorite: null,
      issuein: null,

      issues: [],
      issuesin: [],
      priorities: [],

      errors: [],
      message: null,
      disable: false,
      disableId: false,
      disableMessage: null,
      success: false
    };
  },

  mounted() {
    this.fetchIssues();
    this.getPriority();
    this.getIssueIn();
  },

  methods: {
    add() {
      this.disable = true;
      this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

      if (this.media) {
        this.uploadImage()
          .then(downloadURL => {
            this.image = downloadURL;
            return this.image;
          })
          .then(() => {
            this.uploadData();
          });
      } else {
        this.uploadData();
      }
    },

    fetchIssues() {
      let url = domain + "issue";
      this.issues = [];
      axios
        .get(url, {
          params: {
            submittedBy: this.user.uuid
          },
          headers: {
            "X-api-token": this.user.uuid
          }
        })
        .then(res => {
          if (res.data.length) {
            res.data.forEach(doc => {
              this.issues.push(doc);
            });
          }
        });
    },

    getPriority() {
      let url = domain + "issue/priority";
      this.priorities = [];
      axios
        .get(url, {
          headers: {
            "X-api-token": this.user.uuid
          }
        })
        .then(res => {
          if (res.data.length) {
            res.data.forEach(doc => {
              this.priorities.push(doc);
            });
          }
        });
    },
    getIssueIn() {
      let url = domain + "issue/in";
      this.issuesin = [];
      axios
        .get(url, {
          headers: {
            "X-api-token": this.user.uuid
          }
        })
        .then(res => {
          if (res.data.length) {
            res.data.forEach(doc => {
              this.issuesin.push(doc);
            });
          }
        });
    },

    uploadImage() {
      let file = this.$refs.image.files[0];
      this.image = null;
      return firestorage
        .ref("issues/" + file.name)
        .put(file)
        .then(res => {
          //downloading url
          let mediaURL = res.ref.getDownloadURL();
          return mediaURL;
        })
        .catch(err => {
          console.log(err);
          return null;
        });
    },

    uploadData() {
      let url = domain + "issue";
      axios
        .post(
          url,
          {
            title: this.title,
            issue: this.issue,
            priority: this.priorite,
            in: this.issuein,
            image: this.image
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          this.issues.unshift({
            id: 1,
            title: this.title,
            priority: this.priorite,
            resolved: null
          });

          //// to show

          this.message = "Thankyou for reporting!";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          this.message = "Some error occured";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    validate() {
      this.errors = [];
      if (this.title && this.issue && this.priorite && this.issuein) {
        return true;
      } else {
        if (!this.issue || !this.title) {
          Vue.set(this.errors, 1, "Fill Issue");
        }
        if (!this.priorite) {
          Vue.set(this.errors, 2, "Fill Priority");
        }

        if (!this.issuein) {
          Vue.set(this.errors, 2, "Fill Issue In");
        }

        Vue.set(this.errors, 0, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>



