<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12">
        <div class="card content border-1 shadow-sm mt-3 fixed-card">
          <div class="card-body">
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <h5>STOCK CONTROL</h5>
              </div>
              <div class="col-6 content-header small">
                <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">dashboard</router-link>>
                  <router-link to="/inventory/dashboard" class="cursor-pointer" tag="span">inventory</router-link>>
                  <router-link to="/inventory/stock/control" class="cursor-pointer" tag="span">stock</router-link>
                </span>
              </div>
            </div>
            <div class="row">
              <div class="col-7 text-center">
                <!-- <span>Or</span> -->
              </div>
              <div class="col-5">
                <div class="float-right" v-if="item">
                  <button class="btn btn-danger btn-sm float-right pr-2" @click="deleteItem()">
                    <i class="fas fa-trash pr-1"></i>
                    Delete
                  </button>
                </div>
              </div>
            </div>
            <hr />
            <!-- content -->
            <div class="row" v-if="item">
              <div class="col-12">
                <!-- item detail -->
                <div class="row">
                  <div class="col-12">
                    <div class="text-center">
                      <span>{{ item.name }}</span>
                    </div>
                    <div>
                      <div class="row mt-3">
                        <!-- stock -->
                        <div class="col-6">
                          <span class="font-weight-bold">Available Stock</span>
                          <span>{{ item.stock }}</span>
                        </div>
                        <!-- total stock -->
                        <div class="col-6 text-right">
                          <span class="font-weight-bold">Total Stock</span>
                          <span>{{ item.totalStock }}</span>
                        </div>
                      </div>
                      <div class="row mt-2">
                        <!-- msp -->
                        <div class="col-4">
                          <span class="font-weight-bold">MSP</span>
                          <span>{{ item.msp }}</span>
                        </div>
                        <!-- current price -->
                        <div class="col-4 text-center">
                          <span class="font-weight-bold">Current Price</span>
                          <span>{{ item.currentPrice }}</span>
                        </div>
                        <!-- mrp -->
                        <div class="col-4 text-right">
                          <span class="font-weight-bold">MRP</span>
                          <span>{{ item.MRP }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <hr />
                <!-- stock -->
                <div v-if="user.designation == 'admin' || user.uuid == item.uploadedBy">
                  <div class="row">
                    <div class="col-12 text-center">
                      <div class="mb-2">Amount of Stock:</div>
                      <div>
                        <span class="btn btn-outline-success" @click.prevent="changeStock('inc')">+</span>
                        <span class="pl-1 pr-1">{{ newStock }}</span>
                        <span class="btn btn-outline-success" @click.prevent="changeStock('dec')">-</span>
                      </div>
                    </div>
                  </div>

                  <div class="row">
                    <div class="col-12 text-center mt-2">
                      <label class="font-weight-semibold">Inc/Dec</label>
                      <br />
                      <form class="form-inline justify-content-center">
                        <select v-model="stockChange" name="stockChange" class="custom-select">
                          <option value="inc">Increase</option>
                          <option value="dec">Decrease</option>
                        </select>
                      </form>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-6">
                      <label>MRP</label>
                      <input
                        type="number"
                        class="form-control form-control-sm"
                        required
                        v-model.number="newMRP"
                        :disables="disable"
                      />
                    </div>
                    <div class="col-sm-6">
                      <label>MSP</label>
                      <input
                        type="number"
                        class="form-control form-control-sm"
                        required
                        v-model.number="newMSP"
                        :disables="disable"
                      />
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-6">
                      <label>Current Price</label>
                      <input
                        type="number"
                        class="form-control form-control-sm"
                        required
                        v-model.number="newCurrentPrice"
                        :disables="disable"
                      />
                    </div>
                    <div class="col-sm-6">
                      <label>Any Comments</label>
                      <input
                        type="text"
                        class="form-control form-control-sm"
                        required
                        v-model="comments"
                        :disables="disable"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-12 mt-2">
                      <div class="float-left">
                        <button
                          type="submit"
                          class="btn btn-success btn-sm"
                          @click.prevent="updateStock()"
                        >Update</button>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row" v-else>
                  <div class="col-12 mt-4">
                    <div class="text-center">
                      <img
                        src="../../../assets/exclamation-mark.svg"
                        class="img-fluid align-self-center mb-2"
                        style="width:5em"
                      />
                      <br />
                      <span>You aren't allowed to update stock</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row" v-else>
              <div class="col-12 mt-4">
                <div class="text-center">
                  <img
                    src="../../../assets/exclamation-mark.svg"
                    class="img-fluid align-self-center mb-2"
                    style="width:5em"
                  />
                  <br />
                  <span>Search Some Item</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Loader :disable="disable" :success="success" :message="message" />
      </div>
    </div>
  </div>
</template>

<script>
import { bus } from "@/main";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import db from "@/firebaseInit";
import firebase from "firebase";
import axios from "axios";

export default {
  name: "control_stock",
  data() {
    return {
      item: null,
      newStock: 0,
      newMRP: 0,
      newMSP: 0,
      newCurrentPrice: 0,
      comments: null,
      stockChange: "inc",
      disable: false,
      success: false,
      message: null
    };
  },
  beforeRouteEnter(to, from, next) {
    let url = domain + "item";
    axios
      .get(url, {
        params: {
          productId: to.params.productId
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            vm.item = doc;
            vm.newMRP = doc.MRP;
            vm.newMSP = doc.msp;
            vm.newCurrentPrice = doc.currentPrice;
          });
        });
      });
  },
  methods: {
    changeStock(type) {
      if (type == "inc") {
        this.newStock++;
      } else {
        if (this.newStock > 0) {
          this.newStock--;
        }
      }
    },

    fetchItem() {
      this.item = null;
      let url = domain + "item";
      this.disable = true;
      axios
        .get(url, {
          params: {
            productId: this.searchedData
          }
        })
        .then(res => {
          if (res.data.length > 0) {
            res.data.forEach(doc => {
              this.item = doc;
              this.newMRP = doc.MRP;
              this.newMSP = doc.msp;
              this.newCurrentPrice = doc.currentPrice;
            });
            this.message = "Item Found";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          } else {
            this.message = "Item Not Found";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          }
        })
        .catch(err => {
          this.message = "Some error occured";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    updateStock() {
      if (
        this.user &&
        this.user.status == "active" &&
        (this.user.designation == "admin" ||
          this.user.uuid == this.item.uploadedBy) &&
        this.newStock > 0
      ) {
        if (this.stockChange == "dec") {
          this.newStock = -Math.abs(this.newStock);
        }
        this.disable = true;
        let url = domain + "item/" + this.item.productId;
        axios
          .post(
            url,
            {
              _method: "patch",
              stockChange: this.newStock,
              MRP: this.newMRP,
              msp: this.newMSP,
              currentPrice: this.newCurrentPrice,
              comments: this.comments
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.status == 200 && res.data == "Success") {
              this.newStock = 0;
              this.message = "Data Update Success";
            } else {
              this.message = res.data;
            }
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 3000);
          })
          .catch(err => {
            this.newStock = Math.abs(this.newStock);
            this.message = "Some error occured!!";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 3000);
          });
      } else {
        if (this.newStock <= 0) {
          alert("Please enter some stock");
        } else if (
          this.user.status != "active" ||
          this.user.designation != "admin" ||
          this.user.uuid != this.item.uploadedBy
        ) {
          alert("You can't update this item");
        }
      }
    },

    deleteItem() {
      if (
        this.user &&
        (this.user.uuid == this.item.uploadedBy ||
          this.user.designation == "admin")
      ) {
        if (confirm("You are about to delete " + this.item.name)) {
          let url = domain + "item/" + this.item.productId;
          axios
            .post(
              url,
              {
                _method: "delete"
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data == "done") {
                db.collection("items")
                  .where("productId", "==", this.item.productId)
                  .get()
                  .then(query => {
                    query.forEach(doc => {
                      doc.ref.delete();
                      this.item = null;

                      this.message = "Deleted successfully";
                      this.success = true;
                      this.disable = false;

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    });

                    this.items.splice(index, 1);
                  })
                  .catch(err => {
                    this.item = null;

                    this.success = true;
                    this.disable = false;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              } else {
                alert(res.data);
              }
            });
          this.disable = true;
        }
      } else {
        alert("You are not allowed to delete this");
      }
    }
  }
};
</script>



