<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- content -->
            <div>
              <!-- header -->
              <div class="row mt-4 mb-4">
                <div class="col-6 content-header">
                  <div class="pl-4">
                    <i class="far fa-list-alt"></i>
                    <h5 class="pl-2 d-inline-block">
                      <strong>User's Ordered</strong>
                    </h5>
                  </div>
                </div>
                <div class="col-6">
                  <div class="float-right">
                    <select
                      v-model="filterPeriod"
                      class="custom-select custom-select-sm"
                      @change.prevent="fetchItems(1)"
                    >
                      <option value="0">Today</option>
                      <option value="1">Yesterday</option>
                      <option value="7">Last 7 Days</option>
                      <option value="30">Last 30 Days</option>
                    </select>
                  </div>
                </div>
              </div>
              <hr class="pl-4 pr-4" />
              <div class="table-responsive table-scroll mt-4">
                <table class="table table-hover table-sm table-borderless table-content text-left">
                  <thead>
                    <tr>
                      <th>S. No.</th>
                      <th>Image</th>
                      <th>Name</th>
                      <th>Quantity</th>
                      <th>Cost Price</th>
                      <th>MRP</th>
                      <th>Current Price</th>
                      <th>No of Orders</th>
                    </tr>
                  </thead>
                  <tbody v-if="items.length > 0">
                    <tr v-for="(item, index) in items" :key="item.productId">
                      <td>{{index+1}}</td>
                      <td>
                        <img :src="item.image" width="150" class="img-fluid" />
                      </td>
                      <td>{{item.name}}</td>
                      <td>{{item.qty}} {{item.unit}}</td>
                      <td>{{item.msp}}</td>
                      <td>{{item.MRP}}</td>
                      <td>{{item.currentPrice}}</td>
                      <td>{{item.orders_count}}</td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="9" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fmedia.istockphoto.com%2Fvectors%2Fvector-cartoon-of-skeleton-arm-is-sticking-out-of-the-grave-holding-vector-id666480498%3Fk%3D6%26m%3D666480498%26s%3D170667a%26w%3D0%26h%3DRaDgNagcF-bDMgVwRXq9lMMh-D7Wnvj2wzFr6D_iiJM%3D&f=1&nofb=1"
                            alt="Card image cap"
                            style="width:20em"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! Nothing is ordered. Do something!</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <Loader :disable="disable" :success="success" :message="message" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import Footer from "../Footer.vue";
import { domain } from "@/store/variable";
import axios from "axios";
import { bus } from "@/main";
import store from "@/store/store";

export default {
  name: "userOrderList",
  components: {
    Footer
  },

  data() {
    return {
      items: [],
      filterPeriod: 30,
      disable: false,
      success: false,
      message: null
    };
  },

  computed: {
    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    this.fetchItems();
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];
      let url = domain + "item";
      if (!page) {
        page = 1;
      }
      axios
        .get(url, {
          params: {
            orders: "fetch",
            ordersCompare: ["created_at", ">=", this.filterPeriod],
            limit: this.size,
            page: page,
            order: this.orderItems,
            orderWay: this.orderWay
          }
        })
        .then(res => {
          if (res.data.data.length) {
            res.data.data.forEach(doc => {
              this.items.push(doc);
            });
            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
            this.loading = false;
          }
        })
        .catch(err => {
          this.loading = false;
        });
    },

  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



